#Layercanva {
    height: 100%;
    width: 100%;
}

#CanvasPanel {
    grid-row: span 2;
    position: relative;
}

#CanvasPanel[fileDrop]::before{
    position: absolute;
    height: 100%;
    width: 100%;
    top:0;
    left:0;
    background-color: rgba(0, 0, 0, 0.5);
    content: "";
    display: block;
}

