:root {
    --AccentBlue: #2596be;
    --AccentGreen: #35df91;
    --AccentRed: #f47960;
    --AccentYellow: #fac762;
    --Base: #1a1c22;
    --Base2: #17161c;
    --Base3: #262730;
}

#root {
    min-height: 100vh;
    height: fit-content;
}


body {
    margin: 0;
    background-color: var(--Base2);
    color: #fefefe;
    min-height: 100vh;
    height: fit-content;
    font-family: 'Noto Sans Mono', monospace;
    overflow-y: hidden;
}
