#error{
    position: fixed;
    height: fit-content;
    width: 25rem;
    min-height:10rem;
    left: calc(50% - (25rem / 2));
    top: calc(50% - (10rem / 2));
    color: white;
    background-color: var(--AccentRed);
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius:  2.5rem;
    box-shadow: 5px 5px 20px black;
}

#error > *{
    text-align: center;
}

#error > h1{
    height: min-content;
    margin-bottom: 10px;
    margin-left: 5px;
}

#error > h2{
    margin-left: 5%;
    width: 90%;
    font-weight: 300;
    font-size: large;
}
