
#VFXPanel{
    height: 100%;
    width: 100%;
    grid-column: 1;
    background: var(--Base3);
    grid-row: span 2;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

#VFXPanel > *{
    width: 54px;
    height: 54px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#VFXPanel > * > img{
    width: 36px;
    height: 36px;
}