#LayerPanel{
    background-color: var(--Base3);
    grid-column: 3;
    grid-row:2;
}


#LayerEntry{
    height: 3rem;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

#LayerEntry > input{
    background:  none;
    color: whitesmoke;
    border: none;
    width: calc(90% - 3rem - 10px);
    margin-left: 5px;
    margin-right: 5px;
}

#orderControls{
    width: fit-content;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
#orderControls > img{
    width:100%;
    height: 35%;
}

.layerSelected{
    background-color: var(--Base);
}

.visibilityToggle{
    width: 3rem;
    height: 3rem;
}